* { 
  scroll-behavior: smooth;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background-color: #0c397c;
  width: 100vw;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-size: 3.5vw;
  font-family: 'Nunito', sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {
  text-decoration: unset;
  color: aliceblue;
}

.container-loading {
  background: linear-gradient(45deg, #f5f7fb, #46464600);
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.container-loading img { position: absolute; }

.background-logo-loading { 
  width: 250px;
  animation: App-logo-spin infinite 5s linear;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

::selection {
  background-color: #58036e;
}

body::-webkit-scrollbar {
  width: auto;
}

body::-webkit-scrollbar-track {
  background-color: #0e3575;
  backdrop-filter: blur(10px);
}

body::-webkit-scrollbar-thumb {
  background-color: #6b80ad;
  border-radius: 8px;
  border: 4px solid transparent;
  background-clip: content-box;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: #6b80adb7;
}

@media (max-width: 1023px) {
  body::-webkit-scrollbar {
    display: none;
  }
}
