@import url('https://fonts.googleapis.com/css2?family=Nunito&display=swap');

.particles-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 9;
}

.particle {
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #ffffff63;
  pointer-events: none;
  opacity: 0;
  animation: particleEffect 1s linear infinite;
}

/* #282c34 */
/* backdrop-filter: blur(10px); */

.introdution-container {
  width: 100%;
  height: 100vh;
  background-size: cover;
  overflow-y: hidden;
  position: relative;
  overflow-x: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background: linear-gradient(180deg, rgb(7 6 7), rgb(19 11 20) 70%, rgb(4 7 37));
  box-shadow: 0 2px 5px #7979ff6b;
}

.stars-animation {
  animation: moveBackgrond 15s infinite ease-in-out alternate;
  position: absolute;
  opacity: 0.5;
}

.title-container, .animation-shown {
  animation: hasShown 2s ease-in-out forwards;
}

.animation-shown { opacity: 0; }

.title-container {
  margin: 0;
  width: 42vw;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  z-index: 10;
}

.title-container small { font-size: 2.5vw; }

.title-container h1 {
  color: rgb(164 88 123);
  font-size: 4.5vw;
  text-shadow: 2px 2px 4px #58036e;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  align-content: flex-start;
  place-items: center;
  font-weight: 800;
}

.button-download {
  text-align: center;
  /* border: solid 5px rgb(95 109 239); */
  font-family: sans-serif;
  font-size: large;
  font-weight: 600;
  color: white;
  margin: 0;
  box-shadow: 0px 0px 50px 0px #5f6defab;
  outline: none;
  border: none;
  cursor: pointer;
  height: 60px;
  border-radius: 5px;
  background: #5f6def;
}

.button-download a {
  padding: 20px 80px;
  text-decoration: none;
  color: white;
}

.arrow-down {
  color: white;
  bottom: 4.5vh;
  font-size: xxx-large;
  left: 0;
  margin: 0 auto;
  position: absolute;
  right: 0;
  text-align: center;
  padding: 5px;
  background: #3232320d;
  border-radius: 50%;
}

.arrow-down:hover {
  padding: 5px;
  animation: arrowMoving 600ms ease-in-out alternate infinite;
}

.animation-pulse:hover {
  border-radius: 5px;
  animation: pulse 1.25s ease-in-out infinite;
}

.animation-pulse:active {
  opacity: 0.8 !important;
  box-shadow: #5f6def7d;
}

@keyframes hasShown {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: none;
  }
}

@keyframes arrowMoving {
  from { 
    transform: translateY(10px);
  }
}

@keyframes pulse {
  from { box-shadow: 0 0 0 0 #5f6def; }
  to { box-shadow: 0 0 0 2em transparent; }
}

@keyframes moveBackgrond {
    from {
      overflow-x: hidden;
      transform: translateX(-50px);
    }
}

@keyframes particleEffect {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(2);
  }
}

@media (max-width: 1023px) {
  .stars-animation { height: 100vh; }

  .title-container {
    width: 42vh;
    justify-content: space-between;
  }

  .title-container h1 { font-size: 5vh; }

  .title-container h1 small { font-size: 3.5vh; }

  .button-download {
    font-size: 2vh;
    height: 6vh;
    border-radius: 2vw;
  }
  
  .button-download a {
    padding: 0 15vw;
  }
}
