@import url('https://fonts.googleapis.com/css2?family=Lemonada:wght@300..700&display=swap');

section#contact {
  background-color: #02a856;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 90vh;
  background-image: linear-gradient(149deg, transparent 88%, #0c397c 88.01%), linear-gradient(30deg, #0b336f 12%, transparent 12.01%);
  background-size: 150px 100%;
  background: rgb(238,174,202);
  background: radial-gradient(circle, rgba(238,174,202,1) 10%, rgb(88 139 200) 90%, #5f6def 100%);
}

.container-form-title {
  flex-grow: 1;
  display: flex;
  align-items: center;
}

.container-form-title h1 {
  margin: 0;
  text-shadow: 3px -4px 5px #63a2ff, -1px 1px 0px #fff, 1px -1px 0px #fff, -3px 4px 5px #e85bff;
  font-family: "Lemonada", cursive;
  line-height: 70px;
} 

.container-form-title h1, label {
  color: rgba(240, 248, 255, 0.78);
}

label {
  margin-bottom: 5px;
}

.container-form-button {
  flex-grow: 0.5;
  display: flex;
  align-items: flex-end;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50vw;
  flex-grow: 1;
  justify-content: space-between;
}

form input, form textarea {
  outline: none;
  border: none;
  height: 50px;
  width: 100%;
  padding: 0 15px;
}

form div {
  display: flex;
  flex-direction: column;
  width: 100%;
}

form input {
  padding: 0 15px;
  color: aliceblue;
  background: transparent;
  border: 2px solid #ffffff82;
  border-radius: 1rem 1.5rem;
  font-size: 18px;
}

form label {
  width: 100%;
  text-align: left;
  font-weight: 600;
}

form textarea {
  height: 192px;
  resize: vertical;
  color: aliceblue;
  background: transparent;
  line-height: 20px;
  font-size: 15px;
  resize: none;
  padding: 8px;
  border: 2px solid #ffffffb0;
  border-radius: 1rem 1.8rem;
}

form textarea {
  height: 192px;
  resize: vertical;
  font-size: 20px;
}

input[type="button"] {
  width: 260px;
  padding: 21px;
  text-align: center;
  outline: none;
  border: none;
  font-size: large;
  background: radial-gradient(#388eff, transparent);
  color: #ffffffa8;
  font-weight: 600;
  cursor: pointer;
  opacity: 0.8;
}

textarea::-webkit-scrollbar {
  width: auto;
}

textarea::-webkit-scrollbar-thumb {
  background-color: #ffffff54;
  border-radius: 8px;
  border: 4px solid transparent;
  background-clip: content-box;
}

textarea::-webkit-scrollbar-track {
  background-color: #028745;
  backdrop-filter: blur(10px);
}

label span {
  color: #7443ea;
  margin-left: 10px;
  text-shadow: 
    1px -1px 1px #63a2ff,
    -1px 1px 0px #fff0, 
    1px -1px 0px #fff0, 
    -1px 1px 1px #e85bff;
}

.column-form-background {
  width: 100%;
  height: 130px;
  background-image: linear-gradient(149deg, transparent 50%, #0c397c 50.01%), linear-gradient(30deg, #0b336f 51%, transparent 51.01%);
  background-size: 150px 100%;
}
