footer {
  width: 100%;
  height: 330px;
  display: flex;
  background-color: #0c397c;
  position: relative;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.pattern {
  width: 100%;
  background-image: url('../../assests/img/pattern1.png'); /* Substitua pelo caminho da sua imagem */
  background-repeat: repeat;
  background-size: 450px;
  opacity: 0.1;
  background-position: center;
  position: absolute;
  z-index: 9;
  height: 100%;
}

footer p {
  font-size: large;
  font-weight: 400;
  color: white;
  opacity: 1;
  align-items: center;
  text-align: center;
  width: fit-content;
  height: fit-content;
  display: flex;
  justify-content: center;
  margin: 0;
  z-index: 10;
}

.links-container {
  margin: 0;
  display: flex;
  line-height: normal;
  list-style: none;
  padding: 0;
  align-items: center;
  justify-content: space-between;
  width: 167px;
  z-index: 10;
}

.social-icon {
  padding: 0;
  margin: 0;
  width: 50px;
  text-align: center;
}

.social-icon img {
  padding: 4px;
  background: #14458d;
  border-radius: 5px;
  transition: all 1s ease-in-out;
}

.social-icon img:hover {
  background: linear-gradient(90deg, #7f4bff91, #0c397cab 80%);
}

.scroll-up-button {
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 10;
  opacity: 0;
  transform: translateY(100px);
  transition: all .5s ease;
  outline: none;
  border-radius: 10px 25px;
  width: 60px;
  cursor: pointer;
  box-shadow: 0 0 4px #00000069;
  background: linear-gradient(180deg, #292955, rgb(12 57 124) 100%);
  border: none;
}

.scroll-up-button:hover { background: linear-gradient(180deg, #26264e, rgb(12, 55, 120) 120%); }

.scroll-up-button:active { background: linear-gradient(180deg, #292955, rgb(12 57 124) 120%); }

.show-scroll-button {
  opacity: 1;
  transform: translateY(0)
}

.arrow-top {
  color: #7b62bc;
  rotate: 180deg;
  width: 100%;
  height: 100%;
  padding: 4px;
}

.arrow-top:active { opacity: 0.8; }

.arrow-top:hover {
  transform: translateY(0px);
  animation: arrowMoving 500ms ease-in-out alternate infinite;
}

@keyframes arrowMoving {
  100% { 
    transform: translateY(-5px);
  }
}
