header {
  top: 0;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 70px;
  background-color: #0c397cde;
  display: flex;
  color: white;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 11;
  backdrop-filter: blur(10px);
  box-shadow: 0 0 5px #072c66;
}

header nav {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

header ul {
  list-style: none;
  display: flex;
  gap: 30px;
  margin: 0;
}

header li {
  cursor: pointer;
  width: 85px;
  text-align: center;
}

header a { font-weight: 600; }

header tr {
  align-items: center;
  background: #07214a3d;
  border-radius: 10em;
  color: #0b7add;
  display: flex;
  font-size: large;
  font-weight: 700;
  justify-content: center;
  text-shadow: 2px 2px #09306b;
  width: 90px;
}

header table img[alt="Imagem de Olho"] {
  margin-right: 10px;
  width: 30px;
}

header td {
  display: flex;
  align-items: center;
  line-height: 100%;
}

.container-logo {
  position: relative;
  width: 5vw;
}

img[alt='logo-fundo'] {
  pointer-events: none;
  animation: App-logo-spin infinite 20s linear;
  width: 100%;
  height: 100%;
}

img[alt='log'] {
  width: 2.2vw;
  position: absolute;
  top: 50%;
  left: 50%;
  cursor: pointer;
  transform: translate(-50%, -50%);
}

.nav-item a {
  padding: 20px 0px;
  display: inline-block;
  position: relative;
  opacity: 0.8;
}

.nav-item a:hover { opacity: 1; }

.nav-item a::before {
  transition: 300ms;
  height: 5px;
  content: "";
  position: absolute;
  background-color: #1a6dea;
  border-radius: 5px;
  width: 100%;
  bottom: 5px;
  opacity: 0;
}

.nav-item a:hover::before {
  bottom: 10px;
  opacity: 1;
}

.sandwich-button-container, .mobile-sandwich-button { display: none; }

.mobile-sandwich-button span {
  width: 100%;
  height: 1.11vw;
  background: rgb(58, 115, 173);
  border-radius: 4px;
  transition: all .4s ease;
}

@media (max-width: 1023px) {
  header { 
    width: 100vw; 
    height: 18vw;
  }

  header tr, 
  .container-logo, 
  .sandwich-button-container {
    width: 25vw;
  }

  header tr,
  .mobile-sandwich-button {
    height: 10vw;
  }

  header tr {
    font-size: 5vw;
  }

  header table img[alt="Imagem de Olho"] {
    margin-right: 1vw;
    width: 8.4vw;
  }

  header nav { justify-content: space-between; }

  header ul { display: none; }

  .mobile-sandwich-button {
    display: flex;
    width: 11.2vw;
    border: none;
    background: transparent;
    flex-direction: column;
    justify-content: space-around;
    cursor: pointer;
    background: #0f105638;
    padding: 1.6vw 2.3vw;
    border-radius: 0.5em;
    outline: none;
  }

  .sandwich-button-container {
    display: flex;
    justify-content: center;
  }

  img[alt='log'] { width: 11vw; }

  img[alt='logo-fundo'] {
    pointer-events: none;
    animation: App-logo-spin infinite 20s linear;
    width: 100%;
    height: 100%;
  }
}